</template>

<script>
export default {
  data() {
    return {
      slides: [],                 // 轮播图自动填充
      goods: {                   // 先留空，接口返回后填充
        title: '',
        price: 0,
        oldPrice: 0,
        coupon: '7折',
        sales: 216,
        stock: 0,
        view: 676,
        img: ''
      },
      promos: [
        { tag: '券', txt: '新人首单送20元无门槛代金券' },
        { tag: '减', txt: '订单满300减20' },
        { tag: '免', txt: '单笔购买满两件免邮费' }
      ],
      services: [
        { tag: '保', txt: '7天无理由退货' },
        { tag: '赔', txt: '假一赔十' }
      ],
      descImgs: ['/static/swiper-background-1.png', '/static/swiper-background-2.png'],
      shareList: [
        { icon: 'weixin-fill', txt: '微信好友' },
        { icon: 'moments', txt: '朋友圈' },
        { icon: 'qq-fill', txt: 'QQ好友' },
        { icon: 'qq-circle-fill', txt: 'QQ空间' }
      ],
      specs: [
        {
          name: '尺寸',
          values: [
            { name: 'XS', selected: false },
            { name: 'S', selected: true },
            { name: 'M', selected: false },
            { name: 'L', selected: false },
            { name: 'XL', selected: false }
          ]
        },
        {
          name: '颜色',
          values: [
            { name: '白色', selected: true },
            { name: '珊瑚粉', selected: false },
            { name: '草木绿', selected: false }
          ]
        }
      ],
      quantity: 1,
      specShow: false,
      shareShow: false
    };
  },
  computed: {
    selectedSpec() {
      return this.specs
        .map(g => g.values.find(v => v.selected)?.name)
        .filter(Boolean)
        .join('，');
    }
  },
  onLoad(options) {
    const productId = options.id;
    this.loadProduct(productId);

    // #ifdef MP-WEIXIN
    wx.showShareMenu({ withShareTicket: true, menus: ['shareAppMessage', 'shareTimeline'] });
    // #endif
  },
  onShareAppMessage() {
    return { title: this.goods.title, path: `/pages/detail/detail?id=${this.goods.id}` };
  },
  onShareTimeline() {
    return { title: this.goods.title, query: `id=${this.goods.id}` };
  },
  methods: {
    async loadProduct(id) {
      uni.showLoading({ title: '加载中' });
      try {
        const res = await uni.request({
          url: `/api/good/store/${id}/productDetails`,
          method: 'GET'
        });
        const d = res.data;
        if (!d) throw new Error('商品不存在');

        // 映射到本地 goods
        this.goods = {
          id: d.id,
          title: d.name,
          price: d.price,
          oldPrice: d.price * 1.2,        // 如后端有 oldPrice 可替换
          coupon: '7折',                  // 促销静态示例
          sales: 216,                     // 如后端有 sales 可替换
          stock: d.stock,
          view: 676,                      // 如后端有 view 可替换
          img: d.imageUrl
        };

        // 轮播图：当前只有一张主图，复用 3 次做占位
        this.slides = [d.imageUrl, d.imageUrl, d.imageUrl];
      } catch (e) {
        uni.showToast({ title: '商品信息获取失败', icon: 'none' });
      } finally {
        uni.hideLoading();
      }
    },

    preview(index) {
      uni.previewImage({ current: index, urls: this.slides });
    },
    showSpec() {
      this.specShow = true;
    },
    choose(groupIdx, valIdx) {
      this.specs[groupIdx].values.forEach((v, idx) => {
        this.$set(v, 'selected', idx === valIdx);
      });
    },
    confirmSpec() {
      this.specShow = false;
      uni.showToast({ title: '已选择：' + this.selectedSpec, icon: 'none' });
    },
    toOrder() {
      uni.navigateTo({ url: '/pages/order/detail' });
    },
    toHome() {
      uni.switchTab({ url: '/pages/index/index' });
    },
    toStore() {
      uni.navigateBack();
    },
    shareTo(platform) {
      uni.showToast({ title: '分享到' + platform, icon: 'none' });
    }
  }
};
</script>

